import { ob } from '../utils';

export default function makeLayers() {
  // eslint-disable-next-line
  const images = [];
  document.querySelector('.create-layer').addEventListener('click', () => {
    ob.resetEvents();
    // eslint-disable-next-line
    const color = document.querySelector('.color-ignore').value;
    const canvas = document.querySelector('.c-1');
    const ctx = canvas.getContext('2d');
    const img = document.querySelector('.layers-img img');
    img.style.width = '100px';
    img.style.height = '100px';
    const rgb = ob.hex2rgb(color);
    // eslint-disable-next-line
    const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
    const pixels = imageData.data;
    for (let i = 0; i < pixels.length; i += 4) {
      if (pixels[i] === rgb.r && pixels[i + 1] === rgb.g && pixels[i + 2] === rgb.b) {
        pixels[i + 3] = 0;
      }
    }
    ctx.putImageData(imageData, 0, 0);
    const canvasImg = document.querySelector('.c-1');
    const src = canvasImg.toDataURL();
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    img.src = src;
    images.push(src);
  });
  document.querySelector('.delete-layer').onclick = () => {
    const img = document.querySelector('.layers-img img');
    if (img.src !== undefined) {
      const src = img.getAttribute('src');
      for (let i = 0; i < images.length; i += 1) {
        if (images[i] === src) {
          images.splice(i, 1);
        }
      }
      if (images.length !== 0) {
        img.src = images[images.length - 1];
      } else {
        img.removeAttribute('src');
      }
    }
  };
  document.querySelector('.show-layer').onclick = () => {
    if (images.length !== 0) {
      const img = document.querySelector('.layers-img img');
      let index = 0;
      for (let i = 0; i < images.length; i += 1) {
        if (images[i] === img.src) {
          index = i;
        }
      }
      if (images[index + 1] !== undefined) {
        img.src = images[index + 1];
      } else {
        // eslint-disable-next-line
        img.src = images[0];
      }
    }
  };
  document.querySelector('.add-layer').onclick = () => {
    ob.resetEvents();
    document.querySelector('.canvas').onclick = null;
    const layer = document.querySelector('.layers-img img');
    const ctx = ob.getCtx();
    document.querySelector('.c-1').onclick = (event) => {
      const x = event.offsetX;
      const y = event.offsetY;
      if (layer.src !== undefined) {
        ctx.drawImage(layer, x, y);
      }
      document.querySelector('.c-1').onclick = null;
    };
    // document.querySelector('.canvas').onclick = null;
  };
}
